@import "font.scss";

:root {
  --ochre: #e9b65e;
  --drackOchre: #9f7221;
  --black: #000000;
  --greyL1: #2c2c2c;
  --greyL2: #7a7a7a;
  --white: #ffffff;
  --red: #ff0000;
  --redLight: #ff4949;
  --redDark: #700000;
}

:root[theme="light"] {
  --mainBoldColor: var(--black);
  --textColorAll: var(--black);
  --textColorLink: var(--greyL2);
  --textColorError: var(--red);
  --backgroundColor: var(--white);
  --primary: var(--ochre);
  --secondary: var(--drackOchre);
}

:root[theme="dark"] {
  --mainBoldColor: var(--white);
  --textColorAll: var(--white);
  --textColorLink: var(--greyL2);
  --textColorError: var(--red);
  --backgroundColor: var(--black);
  --primary: var(--drackOchre);
  --secondary: var(--ochre);
}

*,
*:before,
*:after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  list-style: none;
  outline: none;
  border: none;
  font-family: "Manrope", sans-serif;
  font-weight: 500;
  background: transparent;
  font-size: 18px;
  color: var(--textColorAll);
  text-decoration: none;

  transition-property: background-color, color, opacity;
  transition-duration: 0.4s;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Manrope", sans-serif;
  font-weight: 700;
}

h1 {
  font-size: 32px;
}

h2 {
  font-size: 28px;
}

h3 {
  font-size: 24px;
}

h4 {
  font-size: 18px;
}

h5 {
  font-size: 16px;
}

h6 {
  font-size: 12px;
}

a,
button,
b,
strong {
  font-family: "Manrope", sans-serif;
  font-weight: 600;
}


.container {
  max-width: 1200px;
  width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

main,
main.container{
  padding-top: 40px;
}

.header {
  background: var(--primary);
  padding: 15px 30px;
  min-height: 60px;
  display: flex;
  &__navigation{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    height: 100%;
    width: 100%;
  }


  .link {
    border: 0;
    position: relative;
    margin-right: 15px;
    &:before {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 0;
      transition: 0.4s;
      transition-property: width, background-color;
      height: 1px;
      background: var(--textColorAll);
    }

    &:hover {
      color: var(--textColorLink);
      &:before {
        background: var(--textColorLink);
        width: 100%;
      }
    }

    &.active {
      &:before {
        width: 100%;
      }
    }
  }
}