@mixin font($fontName, $fontWeight, $fontWeightNumber, $fontStyle) {
  @font-face {
    font-family: $fontName;
    src: url("../../assets/fonts/" + $fontName + "-" + $fontWeight + ".eot")
        format("eot"),
      url("../../assets/fonts/" + $fontName + "-" + $fontWeight + ".svg")
        format("svg"),
      url("../../assets/fonts/" + $fontName + "-" + $fontWeight + ".ttf")
        format("ttf"),
      url("../../assets/fonts/" + $fontName + "-" + $fontWeight + ".woff")
        format("woff"),
      url("../../assets/fonts/" + $fontName + "-" + $fontWeight + ".woff2")
        format("woff2");
    font-weight: $fontWeightNumber;
    font-style: $fontStyle;
  }
}

@include font("Manrope", "ExtraBold", 800, "noraml");
@include font("Manrope", "Bold", 700, "noraml");
@include font("Manrope", "SemiBold", 600, "noraml");
@include font("Manrope", "Medium", 500, "noraml");
@include font("Manrope", "Regular", 400, "noraml");
@include font("Manrope", "Light", 300, "noraml");
@include font("Manrope", "ExtraLight", 200, "noraml");
