.button {
  margin-top: 8px;
  padding: 8px 16px;
  border-radius: 16px;
  border: 1px var(--mainBoldColor) solid;
  cursor: pointer;
  transition: 0.2s;
  white-space: nowrap;
  &:hover {
    border-radius: 50px;
    transition: 0.6s;
    box-shadow: 0 0 10px var(--mainBoldColor);
    border: 1px transparent solid;
  }
}
