.label {
  margin-bottom: 10px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px var(--mainBoldColor) solid;
  border-radius: 16px;
  overflow: hidden;
  width: 100%;

  .input[type="text"],
  .input[type="password"] {
    padding: 16px;
    width: 100%;
    z-index: 1;
    &:focus + .placeholder,
    &[active="true"] + .placeholder {
      transform: translate(0, -15px) scale(1.5);
      opacity: 0.1;
    }
  }
  .input[type="color"] {
    height: 60px;
    margin: -10px -10px -10px -10px;
    width: calc(100% + 40px);
    cursor: pointer;
    & + .placeholder {
      background: var(--backgroundColor);
      cursor: pointer;
    }
  }
  .placeholder {
    position: absolute;
    cursor: text;
    user-select: none;
    transition: 0.6s;
    white-space: nowrap;
    width: 100%;
    text-align: center;
  }
}
